.breadcrumb {
  padding-bottom: 0.5em; }
  .breadcrumb ol {
    margin: 0;
    padding: 0; }
  .breadcrumb li {
    display: inline;
    margin: 0;
    padding: 0;
    list-style-type: none; }
    .breadcrumb li:before {
      content: ' \BB '; }
    .breadcrumb li:first-child:before {
      content: none; }

[dir='rtl'] .breadcrumb ol {
  margin-right: 0; }
